import { Component, OnInit } from '@angular/core';
import { SelectItem } from 'primeng/primeng';
import { Router } from '@angular/router';
@Component({
  selector: 'app-potential-customer-management',
  templateUrl: 'potential-customer-management.component.html',
  styleUrls: ['potential-customer-management.component.scss']
})
export class PotentialCustomerManagement implements OnInit {
   data: String;
   data1: String;
   data2: String;
   data3: String;
  cities: SelectItem[];
  cities1: SelectItem[];
  cities2: SelectItem[];
  cities3: SelectItem[];
  cities4: SelectItem[];
  value: Date;
  date3: Date;
   type: string = '0';
  constructor(
    public router: Router
  ) {
    this.cities = [
      { label: '请选择', value: '0' },
      { label: '二代身份证', value: '1' },
      { label: '军官证', value: '2' },
      { label: '驾驶证', value: '3' },
      { label: '学生证', value: '4' }
    ];
    this.cities1 = [
      { label: '请选择', value: '0' },
      { label: '男', value: '1' },
      { label: '女', value: '2' },
      { label: '保密', value: '3' },
    ];
    this.cities2 = [
      { label: '请选择', value: '0' },
      { label: '农、林、牧、渔业', value: '1' },
      { label: '金融', value: '2' },
      { label: '房地产', value: '3' },
      { label: '计算机IT', value: '4' }
    ];
    this.cities3 = [
      { label: '请选择', value: '0' },
      { label: '银行/金融/证券/保险', value: '1' },
      { label: '咨询', value: '2' },
      { label: '国家机关单位', value: '3' },
      { label: '独资', value: '4' },
      { label: 'IT/网络/计算机', value: '5' }
    ];
    this.cities4 = [
      { label: '请选择', value: '0' },
      { label: '高管', value: '1' },
      { label: '中层领导', value: '2' },
      { label: '一般员工', value: '3' },
    ];

  }

  ngOnInit() {
    this.data = "基本信息";
    this.data1 = "联系信息";
    this.data2 = "客户概况";
    this.data3 = "客户来源";
  }
  toAdd() {
    if (this.type == '0') {
      this.type = '2';
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/add-customer']);

    } else {
      this.type = '0';
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/add-business']);

    }
  }
  doStatistics() {
    this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/expand']);
  }
  inquire(){
    if (this.type == '0') {
      this.type = '2';
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/inquire-personal']);

    } else {
      this.type = '0';
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/inquire-business']);
    }
  }
  importList(){
     this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/import-list']);
  }
  merge(){
     if (this.type == '0') {
      this.type = '2';
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/merge-personal']);

    } else {
      this.type = '0';
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/merge-business']);
    }
  }
  waiting(){
    this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-potential/waiting']);
 }
}
