import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { PotentialCustomerManagement } from './potential-customer-management.component';//潜在客户管理
import { AddCustomer } from './add/add-customer/add-customer.component';//新增个人客户
import { AddBusiness } from './add/add-business/add-business.component';//新增企业客户
import { Expand } from './expand/expand.component';//潜在客户拓展
import { Inquire } from './inquire/inquire.component';//潜在客户查询
import { InquirePersonal } from './inquire/inquire-personal/inquire-personal.component';//查询-个人
import { InquireBusiness } from './inquire/inquire-business/inquire-business.component';//查询-企业
import { ImportList } from './import-list/import-list.component';//潜在客户名单导入
import { MergePersonal } from './merge/merge-personal/merge-personal.component';//潜在客户合并-个人
import { MergeBusiness } from './merge/merge-business/merge-business.component';//潜在客户合并-企业
import { Waiting } from './waiting/waiting.component';//待合并客户
/**
 * 营销
 * 
 *
 */
export const routes: Routes = [

    {
        path: '',
        component: PotentialCustomerManagement,
        children: [
            {
                path: 'add-customer', component: AddCustomer,
            },
            {
                path: 'add-business', component: AddBusiness,
            },
            {
                path: 'expand', component: Expand,
            },
            {
                path: 'inquire', component: Inquire,
            },
            {
                path: 'inquire-personal', component: InquirePersonal,
            },
            {
                path: 'inquire-business', component: InquireBusiness,
            },
            {
                path: 'import-list', component: ImportList,
            },
            {
                path: 'merge-business', component: MergeBusiness,
            },
            {
                path: 'merge-personal', component: MergePersonal,
            },
            {
                path: 'waiting', component: Waiting,
            },
        ]
    }

]

export const PotentialCusRouting: ModuleWithProviders = RouterModule.forChild(routes);
